.skills {
  &__title {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--dark-color);
    text-align: center;
    margin: 6rem 0 6rem 0;
  }
  &__container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 6rem;
  }
  &__section {
    height: 300px;
    width: 300px;
    padding: 3rem 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgba(255, 255, 255, 0.375);
    box-shadow: 0 0.75rem 2rem 0 rgba(0, 0, 0, 0.1);
    border-radius: 2rem;
    border: 1px solid rgba(255, 255, 255, 0.125);
    &--title {
      font-size: 0.875rem;
      font-weight: 700;
      color: var(--dark-color);
      margin-bottom: 2rem;
    }
  }
}

.list1 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  padding-left: 0rem;
}

.list2 {
  display: grid;
  gap: 1rem;
  padding-left: 0rem;
}

.list3 {
  display: grid;
  gap: 0.7rem;
  padding-left: 0rem;
}
